<template>
  <div class="fkjl clearfix">
       <div class="pag-wrapper clearfix">
             <header class="clearfix">
                <p class="p ">
                  错误记录
                  <span>/   说明说明说明说明说明说明</span>
                </p>
                <div class="wrapper-r clearfix">
                  <router-link tag="p" class="exit" to="/FKJL"> 返回上一级 </router-link>

                </div>
             </header>

             <div style='width:100%;height:500px;background:white;border:1px solid #e8e8f2' class="main">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                   <el-form-item label="支付单号">
                     <el-input v-model="formInline.user" placeholder="请输入订单号"></el-input>
                   </el-form-item>
                   <el-form-item label="运单号">
                     <el-input v-model="formInline.user" placeholder="选择下单区间"></el-input>
                   </el-form-item>
                   <el-form-item label="错误时间">
                     <el-input v-model="formInline.user" placeholder="选择"></el-input>
                   </el-form-item>
                   <el-form-item label="处理状态">
                     <el-input v-model="formInline.user" placeholder="选择"></el-input>
                   </el-form-item>
                   <br>
                   <el-form-item>
                     <el-button type="primary" @click="onSubmit" style="width:190px;height:40px;margin-left:315px;margin-top:15px;">查询</el-button>
                   </el-form-item>
                  </el-form>
                  <div class="hr"></div>



                  <!-- 表格 -->
                  <el-table  ref="multipleTable"  :data="tableData3"  border  tooltip-effect="dark"
                          style="width: 99%;marin:0 auto;" class="table"
                          >
                          <el-table-column
                            type="selection"
                            width="55">
                          </el-table-column>
                          <el-table-column
                            label="支付单号"
                            width="120">
                            <template scope="scope">{{ scope.row.paynumber }}</template>
                          </el-table-column>
                          <el-table-column
                            prop="name"
                            label="运单号"
                            width="120">
                            <template scope="scope">{{ scope.row.ynumber }}</template>
                          </el-table-column>
                          <el-table-column
                            prop="address"
                            label="司机姓名"
                            show-overflow-tooltip width="100">
                            <template scope="scope">{{ scope.row.name }}</template>
                          </el-table-column>
                          <el-table-column prop="address" label="手机号" show-overflow-tooltip width="95">
                              <template scope="scope">
                                <p>{{scope.row.tel}}</p>
                              </template>
                          </el-table-column>
                          <!-- <el-table-column prop="address" label="车牌号" show-overflow-tooltip width="100">
                              <template scope="scope">
                                <p>{{scope.row.carnumber}}</p>
                              </template>
                          </el-table-column>
                          <el-table-column prop="address" label="运费金额" show-overflow-tooltip width="100">
                              <template scope="scope">
                                <p>{{scope.row.ymoney}}</p>
                              </template>
                          </el-table-column> -->
                          <el-table-column prop="address" label="支付金额" show-overflow-tooltip width="100">
                              <template scope="scope">
                                <p>{{scope.row.paymoney}}</p>
                              </template>
                          </el-table-column>
                          <el-table-column prop="address" label="申请时间" show-overflow-tooltip width="100">
                              <template scope="scope">
                                <p>{{scope.row.sqtime}}</p>
                              </template>
                          </el-table-column>
                          <el-table-column prop="address" label="付款时间" show-overflow-tooltip>
                              <template scope="scope">
                                <p>{{scope.row.paytime}}</p>
                              </template>
                          </el-table-column>
                          <el-table-column prop="address" label="失败原因" show-overflow-tooltip>
                              <template scope="scope">
                                <p>{{scope.row.loser}}</p>
                              </template>
                          </el-table-column>
                          <el-table-column prop="address" label="失败时间" show-overflow-tooltip width="150">
                              <template scope="scope">
                                <p>{{scope.row.losertime}}</p>
                              </template>
                          </el-table-column>
                          <!-- <el-table-column prop="address" label="申请时间" show-overflow-tooltip width="150">
                              <template scope="scope">
                                <p>{{scope.row.sqtime}}</p>
                              </template>
                          </el-table-column> -->
                          <el-table-column prop="address" label="操作" show-overflow-tooltip width="150">
                              <template scope="scope">
                                <!-- <span style="cursor:pointer" @click="ifrode=!ifrode">{{scope.row.rode}}</span>
                                <span @click="ishow=!ishow" style="cursor:pointer">{{scope.row.message}}</span> -->
                                <span>{{scope.row.dayin}}</span>
                              </template>
                          </el-table-column>
                        </el-table>
             </div>


      </div>
 </div>
</template>
<router-view></router-view>
<script>
export default {
   data() {
     return {


       formInline: {
         user: '',
         region: '',
         regione: '',
         regiont: '',
         regionh: '',
         regionf: '',
         regioni: '',
         regionb: ''
       },
       form: {
         date1: '',
         date2: ''
       }
       ,tableData3: [{
           date: '2016-05-03',
           name: '王小虎',
           address: '上海市普陀区金沙江路 1518 弄',
           paynumber: '564132135',
           ynumber: '201165545',
           tel: '13588145',

           paymoney: '4679.00',
           sqtime:'2017-01-01',
           paytime: '2016 12-23 10:20:30',
           loser: '账号错误',
           losertime:'2017-01-02',
           dayin:'打印'

         }],
          multipleSelection: []
     }
   },
   methods: {
     onSubmit() {
       console.log('submit!');
     }
   }
 }
</script>
<style lang="less" scoped>
.fkjl{
  width: 1600px;
  background: #fafbff;
  border-top: 1px solid #e6e9f2;
  .pag-wrapper{
    background: #fafbff;
    margin:0 auto;
    width: 92%;
         header{
           height: 100px;

           .p{
             color: #485b93;
             font-weight: 600;
             font-size: 22px;

             margin-top: 20px;
             span{
               color: #8a96ae;
               font-size: 14px;
               font-weight: normal;
             }
           }
           .wrapper-r{
             float: right;
             margin-top: -24px;
             .wrong{
               width:104px;
               height: 39px;
               border:1px solid #8c95be;
               color:#8c95be;
               line-height: 39px;
               text-align: center;
               font-size: 14px;
               float: left;
               border-radius: 3px;
               cursor: pointer;
             }
             .exit{
               float:right;
               width: 104px;
               height: 39px;
               color:#a09c9d;
               line-height: 39px;
               text-align: center;
                margin-left: 10px;
                border-radius: 3px;
                cursor:pointer;
             }

           }
         }

         .main{
           padding-top:40px;
           padding-left:20px;
         }
         .hr{
             border-top: 2px solid #e8e9ee;
             width: 99%;
             margin:20px auto;

         }

  *{
    margin:0;
    padding:0;
    boxing-sizing:border-box;
  }
  }
}
</style>



